import React, { useContext, useEffect, useState } from "react";
import {Link} from "react-router-dom";


import "./login.css";
import  Location from "../../components/location";
import {axiosInstance} from "../../utils/axiosIntance";
import {Redirect,useHistory} from 'react-router-dom'
import { SebedimContext } from "../../context/Sebedim";
import { message } from "antd";
const Login =(props)=>{
    const {dil} = useContext(SebedimContext);
    const history = useHistory();

    const [number,setNumber] = useState("9936");
    const [password,setPassword] = useState();

    const Signin = ()=>{
        let numberr = "+"+number;
        number.length==11 ?
        (axiosInstance.post("/users/login",{
            user_phone: numberr,
            user_password: password
        }).then((data)=>{
            console.log("login data:",data.data);
                message.success(dil=="tm"?"Siz stnlikli ulgama girdiiz":"    ");
                localStorage.setItem("sebetProfile", JSON.stringify(data.data));
                // message.success(dil=="tm"?"Siz stnlikli ulgama girdiiz":"    ")
                //  history.push('/hasabym')
                window.location.href="/"
           
           }).catch((err)=>{
               console.log("login data:",err);
               message.warn(dil=="tm"?"Telefon belgiiz a-da aar sziz gabat gelenok":"      ")
           }))
           :(message.warn(dil=="tm"?"Telefon belgini doly azmaly":"     "))
    }

    const PhoneChangeHundler = (e)=>{
        if(e.length>3 && e.length<12){
            setNumber(e)
        }
    }

    return (
        <div className="login">
            <div className="location">
              <Location name="Login"/>
            </div>
            
            <div className="form">
                <h1>{dil=="tm"?"Ieri gir":""}</h1>
                <h3 style={{fontSize:"18px",
                                textAlign:"left",
                                lineHeight:"22.05px",
                                color:"rgb(64, 55, 55)",
                                fontWeight:"450",
                                marginTop:"30px",
                                marginBottom:"-10px"}}
                                htmlFor="number">{dil=="tm"?"Telefon":""}</h3> <br/>

                <input type="number" name="number" id="nuber" value={number} onChange={(e)=>PhoneChangeHundler(e.target.value)} />

                <h3 style={{fontSize:"18px",
                                textAlign:"left",
                                lineHeight:"22.05px",
                                color:"rgb(64, 55, 55)",
                                fontWeight:"450",
                                marginTop:"30px",
                                marginBottom:"-10px"}}
                        htmlFor="password">{dil=="tm"?"Aar sz":""}</h3> <br/>

                <input type="password" name="password" id="password" onChange={(e)=>setPassword(e.target.value)} />
                <h2 style={{textDecoration:"underline",cursor:"pointer"}} onClick={()=>history.push("/forget")}>{dil=="tm"?"Aar szini unutdym":" "}</h2>
                {/* <Link to="/hasabym"> */}
                    <button style={{cursor:"pointer"}} onClick={()=>Signin()}>{dil=="tm"?"Ieri gir":""}</button>
                    {/* </Link> */}
                <h2><Link to="/signup">{dil=="tm"?"Agza bolmak":""}</Link></h2>
            </div>

        </div>
    );
};

export default Login;